import React, { useCallback, useContext, useEffect, useReducer, useRef, useState } from 'react'
import Navbar from '../../navbar'
import Enlarge from '../../../components/Enlarge'
import Toolbar from '../../toolbar'
import StylePicker from './StylePicker'
import GoToNext from '../../../components/GoToNext'
import ModePicker from '../ModePicker'
import { ItemContext } from '../../../contexts/ItemContext'
import { MakeContext } from '../../../contexts/MakeContext'
import ViewPort from '../../../components/ViewPort'
import { stageJS, uniqlo } from '../../../metalib'
import loadImages, { loadImage } from '../../../utils/loadImages'
import img1 from '../../../assets/texture/01.png'
import requestAnimationFrame from '../../../utils/requestAnimationFrame'
import invoke from '../../../utils/invoke'
import RevokeComponent from '../../../components/revoke'
import getEventPoint from '../../../utils/getEventPoint'
import instruction from '../../../utils/instruction'
import constants from '../../../constants/instruction'
import Instruction from '../Instruction'
import injectImages from '../../../hoc/injectImages'
import AlertModal from '../../../components/AlertModal'
import imageTools from '../../../utils/imageTools'
const style = {
  position: 'absolute',
  left: 0,
  right: 0,
  bottom: 0,
  top: 0,
  overflow: 'hidden'
}

let stop = true

function animate (control) {
  if (control && !stop) {
    control.onEnterFrame(null)
    requestAnimationFrame(animate.bind(null, control))
  }
}

function MomaEarly (props) {
  const { mixData, isLayerTop } = props
  const canvasRef = useRef()
  const imageRef = useRef()
  const multiplyRef = useRef()
  const { multiply, areaStyle, setZoom, imageSrc, canvasArea, canvasSize } = useContext(ItemContext)
  const { width: canvasWidth, height: canvasHeight } = canvasArea
  const [showStatus, setShowStatus] = useState(false)
  const { appid, layers, previousPage } = useContext(MakeContext)
  // const layers = JSON.parse(localStorage.getItem('layers'));
  const modePickerVisible = layers.length > 1 && !isLayerTop
  const [selectedMode, setSelectedMode] = useState(uniqlo.BlendMode.NORMAL)
  const [images, setImages] = useState([])
  const [background, setBackground] = useState()
  const [selectedFilterType, setSelectedFilterType] = useState(0)
  const [isNew, setIsNew] = useState(true)
  const [drag, setDrag] = useState(false)
  const [firstVisited, setFirstVisited] = useState('')
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const [control, setControl] = useState()
  const change = useCallback(function change () {
    setShowStatus(!showStatus)
    setTimeout(() => {
      setShowStatus(!showStatus)
    }, 100)
  }, [showStatus])

  useEffect(() => {
    loadImages(isLayerTop ? undefined : mixData, props.data, img1)
      .then(setImages)
  }, [isLayerTop, mixData, props.data])

  useEffect(() => {
    if (multiply) {
      loadImage(imageSrc)
        .then(setBackground)
    }
  }, [multiply, imageSrc])

  const cancelDesign = useCallback(function cancelDesign () {
    if (control) {
      control.clear()
    }
  }, [control])

  const stringify = useCallback(async function stringify () {
    function storeImage (imageData, data) {
      return Promise.all([
        imageTools.storeImageData({ imageName: `back-${layers.length}`, imageData }),
        isLayerTop ? imageTools.storeImageData({ imageName: 'topLayer', imageData: data }) : undefined
      ])
    }
    const { current } = canvasRef
    if (current && !drag) {
      const data = current.toDataURL()
      const imageData = isLayerTop ? mixData || imageTools.emptyData : data

      stopAcceleratorMonitor()

      // awaittry catchupdate by  -- 20190614
      try {
        await invoke('setLayers', layers)
        await storeImage(imageData, data)

        invoke('navigateTo', {
          to: '/make/Done',
          params: { previousPage }
        })
      } catch (error) {
        setAlertModelVisible(true)
      }
    }
  }, [canvasRef, layers, drag])

  async function fixUseEffectWarn () {
    let visited = await instruction.checkInstruction(constants.Shake_Instruction)
    if (!visited) {
      setFirstVisited('Y')
      await instruction.setInstructionVisited(constants.Shake_Instruction)
      setTimeout(() => {
        setFirstVisited('N')
      }, 3000)
    } else {
      setFirstVisited('N')
    }
  }

  // 
  useEffect(() => {
    fixUseEffectWarn()
  }, [])

  useEffect(() => {
    if (control) {
      control.setStyle(selectedFilterType)
    }
  }, [control, selectedFilterType])

  useEffect(() => {
    if (control) {
      control.blendMode = selectedMode
    }
  }, [control, selectedMode])

  useEffect(() => {
    const { current } = imageRef
    if (current && images.length) {
      const [, front] = images
      current.src = front.src
      current.style.display = 'block'
      setTimeout(() => {
        current.style.opacity = 0
        setZoom(true)
        setTimeout(() => {
          current.style.display = 'none'
        }, 400)
      }, 1000)
    }
  }, [imageRef, images])

  useEffect(() => {
    if (control) {
      const { onEnterFrame } = uniqlo.MOMA02Impl.prototype
      const { current } = multiplyRef
      if (current && background) {
        control.onEnterFrame = (...args) => {
          onEnterFrame.call(control, ...args)
          const { current: canvas } = canvasRef
          imageTools.multiplyImage(background, canvas, canvasArea, canvasSize, current)
        }
      } else {
        control.onEnterFrame = onEnterFrame
      }
    }
  }, [control, canvasArea, background, canvasSize, multiplyRef, canvasRef])

  function stopAcceleratorMonitor () {
    stop = true
    invoke('stopAcceleratorMonitor', {})
  }

  function startAcceleratorMonitor (control) {
    stop = false
    animate(control)
    invoke('startAcceleratorMonitor')
  }

  useEffect(() => {
    const { current } = canvasRef
    if (current && images.length) {
      const [back, front, p] = images
      const stage3D = new stageJS.Stage3D(current)
      const backBitmap = back && stageJS.BitmapData.fromImageElement(back)
      const srcBitmap = stageJS.BitmapData.fromImageElement(front)

      const p1 = stageJS.BitmapData.fromImageElement(p)
      const control = new uniqlo.MOMA01Impl(false, p1)
      setControl(control)
      control.acc_l = 1
      control.changeNextBtnCallback = function (enabled) {
        setIsNew(!enabled)
      }

      control.initWithData(stage3D, backBitmap, srcBitmap)
      control.setStyle(selectedFilterType)
      control.startRendering()

      function handleMouseDown (e) {
        setDrag(true)
        const { x, y } = getEventPoint(e)
        control.onMousePress(x, y)
      }

      function handleMouseMove (e) {
        const { x, y } = getEventPoint(e)
        control.onMouseDrag(x, y)
      }

      function handleMouseUp (e) {
        setDrag(false)
        const { x, y } = getEventPoint(e)
        control.onMouseUp(x, y)
      }

      let timeoutId
      function handleMessage ({ data }) {
        const { x, y } = JSON.parse(data)
        const { mAcc_x, mAcc_y, FACTOR } = control
        if (mAcc_x == 0 && mAcc_y == 0) {
          control.mAcc_x = x
          control.mAcc_y = y
          control._accelerationX = 0
          control._accelerationY = 0
          control.countUpForce = 0
        } else {
          control._accelerationX = (mAcc_x - x)
          control._accelerationY = (mAcc_y - y)
          control.mAcc_x = (x * FACTOR) + (mAcc_x * (1 - FACTOR))
          control.mAcc_y = (y * FACTOR) + (mAcc_y * (1 - FACTOR))
        }
        if (timeoutId) {
          clearTimeout(timeoutId)
        }
        timeoutId = setTimeout(() => {
          control.mAcc_x = 0
          control.mAcc_y = 0
          control._accelerationX = 0
          control._accelerationY = 0
        }, 100)
      }

      // function handlePageFocusChange ({ data }) {
      //   data = JSON.parse(data)
      //   if (data) {
      //     startAcceleratorMonitor()
      //   }
      // }

      let GL

      function handleStateChange ({ type, prevAppid, nextAppid }) {
        if (type === 'push' && prevAppid === appid) {
          GL = stageJS.Context3D.GL
          stopAcceleratorMonitor()
        }
        if (type === 'pop' && nextAppid === appid && GL) {
          stageJS.Context3D.GL = GL
          startAcceleratorMonitor(control)
        }
      }
      // current.addEventListener('mousedown', handleMouseDown, false)
      current.addEventListener('touchstart', handleMouseDown, false)
      // current.addEventListener('mousemove', handleMouseMove, false)
      current.addEventListener('touchmove', handleMouseMove, false)
      // current.addEventListener('mouseup', handleMouseUp, false)
      current.addEventListener('touchend', handleMouseUp, false)
      window.addEventListener('UTmeShakeMessage', handleMessage, false)
      // window.addEventListener('UTmePageFocus', handlePageFocusChange, false)
      global.AppManager.appRouterEvent.addEventListener('UTmeStateChange', handleStateChange)
      startAcceleratorMonitor(control)

      return () => {
        control.dispose()
        // current.removeEventListener('mousedown', handleMouseDown, false)
        current.removeEventListener('touchstart', handleMouseDown, false)
        // current.removeEventListener('mousemove', handleMouseMove, false)
        current.removeEventListener('touchmove', handleMouseMove, false)
        // current.removeEventListener('mouseup', handleMouseUp, false)
        current.removeEventListener('touchend', handleMouseUp, false)
        window.removeEventListener('UTmeShakeMessage', handleMessage, false)
        // window.removeEventListener('UTmePageFocus', handlePageFocusChange, false)
        global.AppManager.appRouterEvent.removeEventListener('UTmeStateChange', handleStateChange)
        stopAcceleratorMonitor()
      }
    }
  }, [canvasRef, images])

  const canvasStyle = multiply ? { ...areaStyle, opacity: 0 } : areaStyle

  return (
    <div style={style}>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
      <div style={{ display: firstVisited === 'N' ? 'block' : 'none' }}>
        <Navbar disabled={drag} back={stopAcceleratorMonitor} revoke={change}>
          <GoToNext onClick={stringify} disabled={isNew} />
        </Navbar>
        <ViewPort topLayer={props.topLayer} data={isLayerTop && mixData}>
          <canvas
            ref={canvasRef}
            width={canvasWidth}
            height={canvasHeight}
            style={canvasStyle}
          />
          <img ref={imageRef} style={{ ...areaStyle, display: 'none', transition: 'opacity .3s ease-in' }} />
          {multiply && <canvas width={canvasWidth} height={canvasHeight} style={{ ...areaStyle, pointerEvents: 'none' }} ref={multiplyRef} />}
        </ViewPort>
        <Toolbar style={{
          display: 'flex',
          justifyContent: 'space-between'
        }}>
          {
            modePickerVisible ? <ModePicker disabled={drag} selectedMode={selectedMode} setSelectedMode={setSelectedMode} /> : ''
          }
          <StylePicker disabled={drag} selectedFilterType={selectedFilterType} setSelectedFilterType={setSelectedFilterType} center={modePickerVisible} />
          <Enlarge position='relative' disabled={drag} />
        </Toolbar>
        <RevokeComponent pageMark='mix' resetLayers={stopAcceleratorMonitor} cancelDesign={cancelDesign} show={showStatus} setShow={setShowStatus} />
      </div>
      <Instruction onClick={() => {
        setFirstVisited('N')
      }} firstVisited={firstVisited} />
    </div>
  )
}

export default injectImages('front')(MomaEarly)
